
import './App.css';
import List from './mycomponent/List'
import Search from './mycomponent/Search'
import useSemiPersistenceState from './Hooks/useSemiPersistenceState';
import {useState} from 'react'

const item = [
  {
      title:"React",
      url:"https://reactjs.org/",
      author:"  jordan walki",
      num_comments:3,
      points:4,
      objectID:0,
  },
  {
      title:"Redux",
      url:"https://redux.js.org/",
      author:"  non abramov,andrew clark",
      num_comments:2,
      points:5,
      objectID:1,
  },
  {
    title:"cars",
    objectID:2,
  },
  {
    title:"grocery",
    objectID:3,
  },
  {
    title:"aircraft",
    objectID:4,
  },
  {
    title:"bucket",
    objectID:5,
  }
];

function App() {
  const[searchTerm,setSearchTerm] = useSemiPersistenceState("customSearchTerm","");
  const[iten,setIten]=useState(item);
  const handleOnSearch = (e) =>{
    setSearchTerm(e.target.value);  
  };
  const onHandleDelete=(name)=>{
    const newlist = iten.filter((i)=>name.objectID!== i.objectID);
    setIten(newlist);
  }
  const filteredItem = iten.filter((ite) => ite.title.toLowerCase().includes(searchTerm.toLowerCase()))
  return (
    <div>

      <Search  searchTerm = {searchTerm} onSearch={handleOnSearch}/>
       <h3>SEARCHING FOR:{searchTerm}</h3>
      <List item={filteredItem} deleted={onHandleDelete}/>
      </div>
  );
}

export default App;